import Button from "@/components/elements/Button";
import { UserProjectType } from "@/types/common";
import React, { useState } from "react";
import styles from "../modalForm.module.scss";

type Props = {
  onCloseClick: () => void;
  onCreateClick: (projectData: Partial<UserProjectType>) => void;
};

const CreateProjectForm: React.FC<Props> = ({
  onCloseClick,
  onCreateClick,
}) => {
  const [projectName, setProjectName] = useState("");
  const [projectURL, setProjectURL] = useState("");
  const [error, setError] = useState("");

  const onInputChange =
    (type: string) => (e: React.ChangeEvent<HTMLInputElement>) => {
      switch (type) {
        case "projectName": {
          setProjectName(e.target.value);
          break;
        }
        case "projectURL": {
          setProjectURL(e.target.value);
          break;
        }
      }
    };

  const validateFields = () => {
    if (!projectName) {
      setError("projectNameError");
      return false;
    }
    if (!projectURL || !projectURL.match(/^([A-Za-z0-9_]+\.)+[A-Za-z0-9_]+$/)) {
      setError("projectURLError");
      return false;
    }
    return true;
  };

  const closeClick = () => {
    clearForm();
    onCloseClick();
  };

  
  const clearForm = () => {
    setProjectName('');
    setProjectURL('');
    setError("");
  };


  const createClick = () => {
    if (!validateFields()) return;
    onCreateClick({ projectName, projectURL });
    clearForm();
    onCloseClick();
  };
  return (
    <div className={styles.modalForm}>
      <div className={styles.fieldsWrapper}>
        <label>
          Project name
          <input
            type="text"
            autoFocus={true}
            className={error === "projectNameError" ? 'error' : ""}
            value={projectName}
            placeholder="project name"
            onChange={onInputChange("projectName")}
          />
        </label>
        <label>
          Project URL
          <input
            type="url"
            className={error === "projectURLError" ? 'error' : ""}
            value={projectURL}
            placeholder="example.com"
            onChange={onInputChange("projectURL")}
          />
        </label>
      </div>
      <div className={styles.buttonsWrapper}>
        <Button text="Create" onClick={createClick} />
        <Button text="Cancel" onClick={closeClick} />
      </div>
    </div>
  );
};

export default CreateProjectForm;
