import React, { useRef } from "react";
import store from "@/MST/store";
import { observer } from "mobx-react-lite";
import { Link } from "react-router-dom";
import { AuthForm } from "@/components";
import Button from "../elements/Button";

import { UserType } from "@/types/common";

import styles from "./AppBar.module.scss";

type Props = {};

const AppBar: React.FC<Props> = () => {
  const modalRef = useRef<HTMLDialogElement>(null);

  const { userIsAuthenticated, userRegister, userLogin, userLogout } = store.user;

  const onAuthBtnClick = () => {
    if (userIsAuthenticated) {
      onLogoutClick();
    } else {
      modalRef.current!.showModal();
    }
  };


  const onRegisterClick = ({ userEmail, userPassword }: UserType) => {
    userRegister({ userEmail, userPassword });
    closeAuthForm();
  };

  const onLoginClick = ({ userEmail, userPassword }: UserType) => {    
    userLogin({ userEmail, userPassword });
    closeAuthForm();
  };

  const onLogoutClick = () => {
    userLogout();
  };

  const closeAuthForm = () => {
    modalRef.current!.close();
  };

  return (
    <header>
      <ul className={styles.navMenu}>
       
        <li>
          <Link to="/cabinet">Cabinet</Link>
        </li>
        <li>
          <Link to="/">Landing</Link>
        </li>
        <li>
          <Button
            onClick={onAuthBtnClick}
            text={userIsAuthenticated ? "log out" : "log in / register"}
          />
        </li>
      </ul>
      <dialog ref={modalRef}>
        <AuthForm
          onCloseClick={closeAuthForm}
          onLoginClick={onLoginClick}
          onRegisterClick={onRegisterClick}
        />
      </dialog>
    </header>
  );
};

export default observer(AppBar);
